.plans-container{
margin-top: -4rem;
padding: 0 2rem;
display: flex;
flex-direction: column;
gap: 4rem;
position: relative;
}

.btn-1{
    background-color: white;
    padding: 0.5rem;
    font-weight: bold;
    border: 4px solid transparent ;
    transition: 300ms all;
    display: block;
    width: 100%;
    margin-top: 10px;
   }
   
.btn-1:hover{
 cursor: pointer;
}

.plans{
 display: flex;
 align-items: center;
 justify-content: center;
 gap: 3rem;
}
.plan{
 display: flex ;
 flex-direction: column;
 background: var(--caloryCard);
color: white;
gap: 2rem;
padding: 1.5rem;
width: 15rem;
}

.plan:nth-child(2){
    background: var(--planCard);
    transform: scale(1.1);
  

}

.plan>svg{
 fill: var(--orange);
 width: 2rem;
 height: 2rem;
}

.plan:nth-child(2) svg{
fill: thistle;
width: 2.5rem;
height: 2.5rem;
}


.plan:nth-child(2) .btn-1{
    color: var(--orange);
 }





.plan>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;

}


.plan>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;

}

.plan>:nth-child(5){
    font-size: 1rem;

}

.features{
 display: flex;
 flex-direction: column;
 gap: 1rem;
}

.feature{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.feature>img{
width: 1rem;
}

.blur-p{
width: 32rem;
height: 23rem;
top: 6rem;
left: 0;

}

.blur-p2 {
    width: 32rem;
    height: 23rem;
    top: 10rem;
    right: 0;
    
}

@media screen  and (max-width:768px){
.plans{
flex-direction: column;
}

.plan:nth-child(2){
transform: none;
}
    
}





