.join{
display: flex;
padding: 0 2rem;
gap: 10rem;
}

.left-j{
color: white;
font-size: 3rem;
font-weight: bold;
text-transform: uppercase;
position: relative;
}

.left-j>hr{
position: absolute;
width: 12rem;
border: 2px solid var(--orange);
border-radius: 10%;
margin-top: -1rem;
}

.right-j{
display: flex;
justify-content: center;
align-items: flex-end;
}
.mail{
display: flex;
gap: 3rem;
background-color: gray;
padding: 1rem 2rem;
}

.mail>input{
background-color: transparent;
border: none;
outline:none ;
color: var(--lightgray);
}

::placeholder{
color: var(--lightgray);
}

.btn-join{
background: var(--orange);
color:white;
}

@media screen  and (max-width:768px){
 .join{
 flex-direction: column;
 gap: 1rem;

 }

 .left-j{
  font-size: x-large;
  flex-direction: column;

 }

 .right-j{
  padding: 2rem;
 }
    
}
